<template>
  <div class="header">
    <div class="layer-container">
      <img src="../../static/header-animation/background.svg" class="layer background" :style="background.style">
        <img v-for="n in 5" :src="'/assets/header-animation/cloud'+(n%3)+'.svg'" class="layer cloud" :style="cloud.style" :key="n">
      <img src="../../static/header-animation/foreground.svg" class="layer foreground" :style="foreground.style">
    </div>
    {{foreground.style}}
  </div>
</template>

<script>
  /* eslint-disable prefer-template,max-len */

  export default {
    name: 'big-header',
    data() {
      return {
        x: 0,
        y: 0,
        width: 0,
        height: 0,
        xfactor: 30,
        yfactor: 10,
        background: {
          style: {
            transform: 'translateZ(400px)',
          },
        },
        cloud: {
          style: {
            transform: 'translateZ(-100px)',
          },
        },
        foreground: {
          style: {
            transform: 'translateZ(80px)',
          },
        },
      };
    },
    mounted() {
      this.width = this.$el.clientWidth;
      this.height = this.$el.clientHeight;
      this.interval = setInterval(this.updateClouds, 100);
      this.$el.addEventListener('mousemove', this.mouseMove.bind(this));
    },
    beforeDestroy() {
      clearInterval(this.interval);
      this.$el.removeEventListener('mousemove', this.mouseMove);
    },
    methods: {
      updateClouds() {
        this.cloud1.x = this.cloud1.x + 1;
        this.cloud2.x = this.cloud2.x + 1;
        this.cloud3.x = this.cloud3.x + 1;
      },
      mouseMove(event) {
        this.x = event.clientX;
        this.y = event.clientY;

        console.log(this.width, this.height);

        this.$set(this.background, 'style', {
          transform: 'translateZ(400px) translateX(' + (((20 / this.width) * this.x) - 10) + 'px) translateY(' + ((20 / this.height) * this.y) + 'px)',
        });

        this.$set(this.cloud, 'style', {
          transform: 'translateZ(-100px) translateX(' + (((60 / this.width) * this.x) - 30) + 'px) translateY(' + ((10 / this.height) * this.y) + 'px)',
        });

        this.$set(this.foreground, 'style', {
          transform: 'translateZ(80px) translateX(' + (((10 / this.width) * this.x) - 5) + 'px)',
        });
      },
    },
  };
</script>

<style lang='scss'>

  .header {
    height: 100%;
    width: 100%;
    .layer-container {
      width: 100%;
      height: 100%;
      background: #29ABE2;
      position: relative;
      perspective: 2000px;
      overflow: hidden;
      .layer {
        height: auto;
        position: absolute;
        &.background {
          width: 100%;
          bottom: 0%;
        }
        &.foreground {
          width: 100%;
          min-width: 400px;
          bottom: 0%;
        }
        &.cloud {
          width: 50%;
          top: 50px;
          max-width: 500px;
        }
      }
    }
  }

</style>



